body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.about{
    text-align: center;  
}

.seperator-blob {
    height: 5px;
    width: 100%;
    display: flex;
    justify-content: center;
     
}

.seperator-blob div {
    width: 15%;
    border-radius: 10px;
    background-color: orangered;
      
}
.container{
    display: flex;
    width: 80%;
    box-shadow: 0 0 20px -2px #1f2235;
    padding-left: 0px;
    
}
.about-me-profile{
    width: 50%;
    background-image: url(/myportfolio-/static/media/img2.aa656605.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
}

.about-me-details{
    width: 48%;
    text-align: justify;
    padding: 30px;

}
.highlight {
    display: flex;
    align-items: center;
    margin: 5px 0;
    font-size: 15px;
}
.highlight-blob {
    height: 12px;
    width: 12px;
    margin: 0 12px 0 0;
    background-color: #ff5823;
    border-radius: 50%;
    margin-left: 20px;
    
}

.about-me-options{
    display: flex;
    margin-top: 150px;
    align-items: center;
    justify-content: center;
}
.about-me-options .highlighted-btn {
    margin: 0 0 0 30px;
}
.primary-btn {
    color: var(--white);
    font-size: 12px;
    background-color: #ff5823;
    color: white;
}
.btn {
    border-radius: 50px;
    padding: 14px 0;
    width: 160px;
    cursor: pointer;
    transition: .2s;
    font-family: "Poppins SemiBold";
}
.highlighted-btn {
    color: var(--white);
    font-size: 12px;
    background-color: #ff5823;
    color: white;
}
.btn {
    border-radius: 50px;
    padding: 14px 0;
    width: 160px;
    cursor: pointer;
    transition: .2s;
    font-family: "Poppins SemiBold";
}



 @media screen and (max-width: 600px){

     .container{
        display: flex;
        padding-left: 0px;
        padding-right: 0px;
        border-radius: 20px;
        width: 100%;
       
     }
     
     .about-me-details{
         width: 100%;
        background-image: url(/myportfolio-/static/media/img2.aa656605.jpeg);
        background-size: cover;
        background-repeat: no-repeat;
        color: white;  
        border-radius: 20px;


     }
     
 }


