}

.title{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0px 110px  50px 0px;
    font-size: large;

}
.image{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    height: 340px;
    width: 340px;
    margin: 0px 0px  0px 100px;
    border: 2px solid white;
    
}
.image .bg{
    height: 320px;
    width: 320px;
    background-image: url(/myportfolio-/static/media/profile.b58d8fab.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
}
.image .bg:hover{
     transition: 1.5s ease-out;
     -webkit-transform: scale(1.05);
             transform: scale(1.05);
}
